











































































import Vue from 'vue';
import axios from 'axios';
import {Util} from '../shared/Util';
import store from '../../store';
export default Vue.extend({

  methods: {
    closeErrorUser(){
      this.isUserExists = false;
    },

    goToQuestions(){
      var flag = true
      if (this.email == "") {
        this.isEmailError = true
        flag = false
      }
      if (this.industryId == 0) {
        this.isIndustryError = true
        flag = false
      }
      Util.getPublicHTTP('').post('user/check', {
        email: this.email,
      })
        .then(response => {
          if (response.data) {
            flag = false;
            this.isUserExists = true;
          }
          if (flag) {
            this.$store.state.questionEmail = this.email
            this.$store.state.questionIndustryId = this.industryId
            this.$router.push({
              path: "questions"
            })
            ;

          }
        });

    }

  },

  created(){
    Util.getPublicHTTP('').get('industry', {})
      .then(response => {
        this.industries = response.data;
      });

  },

  data(){
    return ({
      email: '',
      industryId: 0,
      industries: [],
      isEmailError: false,
      isIndustryError: false,
      isUserExists: false
    })
  }

});



