import axios from 'axios';
import {Util} from '../shared/Util';


export default Vue.extend({
  created() {
    if (!Util.isLoggedIn()) {
      this.$router.push('login')
    }
  },
  methods: {
    closeMessage(){
        this.isMessageActive=false;
    }
  },
  data(){
    return {
        isMessageActive:false,
        isMessageType:'is-primary'
    }
  }
});



