















































import Vue from 'vue';
import axios from 'axios';
import {Util} from '../shared/Util';
export default Vue.extend({
  created() {
  },
  methods: {
      createUser(){
        Util.getHTTP('').post('user', {
                email:this.email,
                pwd:this.pwd,
                type:this.type
        })
          .then(response => {
            this.$router.push('/')
          });
      }
  },
  data(){
    return {
      email: null,
      pwd: '',
      type: 1,
    }

  }
});



